'use client';

import React from 'react';

interface SimpleChartProps {
    data: Array<{
        date: string;
        score: number;
    }>;
    className?: string;
}

export const SimpleChart: React.FC<SimpleChartProps> = ({ data, className = '' }) => {
    if (!data || data.length === 0) {
        return (
            <div className={`flex items-center justify-center bg-graphite-50 dark:bg-graphite-950 rounded-xl border border-dashed border-graphite-200 dark:border-graphite-800 ${className}`}>
                <p className="text-sm text-graphite-400 italic">No data to display</p>
            </div>
        );
    }

    const width = 800;
    const height = 240;
    const padding = { top: 20, right: 30, bottom: 40, left: 50 };
    const chartWidth = width - padding.left - padding.right;
    const chartHeight = height - padding.top - padding.bottom;

    // Calculate points for the line
    const maxScore = 100;
    const minScore = 0;
    const points = data.map((d, i) => {
        const x = padding.left + (i / (data.length - 1 || 1)) * chartWidth;
        const y = padding.top + chartHeight - ((d.score - minScore) / (maxScore - minScore)) * chartHeight;
        return { x, y, ...d };
    });

    // Create path for the line
    const linePath = points.map((p, i) =>
        `${i === 0 ? 'M' : 'L'} ${p.x} ${p.y}`
    ).join(' ');

    // Create path for the filled area
    const areaPath = `${linePath} L ${points[points.length - 1].x} ${height - padding.bottom} L ${padding.left} ${height - padding.bottom} Z`;

    // Format date labels
    const formatDate = (dateStr: string) => {
        try {
            const d = new Date(dateStr);
            return d.toLocaleDateString('en-US', { weekday: 'short' }).slice(0, 2).toUpperCase();
        } catch {
            return '';
        }
    };

    return (
        <div className={`w-full ${className}`}>
            <svg viewBox={`0 0 ${width} ${height}`} className="w-full h-full">
                {/* Grid lines */}
                {[0, 50, 100].map(score => {
                    const y = padding.top + chartHeight - ((score - minScore) / (maxScore - minScore)) * chartHeight;
                    return (
                        <g key={score}>
                            <line
                                x1={padding.left}
                                y1={y}
                                x2={width - padding.right}
                                y2={y}
                                stroke="currentColor"
                                strokeDasharray="3 3"
                                className="text-graphite-700 dark:text-graphite-700"
                                opacity={0.3}
                            />
                            <text
                                x={padding.left - 10}
                                y={y + 4}
                                textAnchor="end"
                                className="text-[11px] font-mono font-semibold fill-graphite-500"
                            >
                                {score}
                            </text>
                        </g>
                    );
                })}

                {/* Area fill */}
                <path
                    d={areaPath}
                    fill="url(#gradient)"
                    opacity={0.2}
                />

                {/* Line */}
                <path
                    d={linePath}
                    fill="none"
                    stroke="#0ea5e9"
                    strokeWidth={2.5}
                    strokeLinecap="round"
                    strokeLinejoin="round"
                />

                {/* Data points */}
                {points.map((p, i) => (
                    <circle
                        key={i}
                        cx={p.x}
                        cy={p.y}
                        r={4}
                        fill="#0ea5e9"
                        stroke="#fff"
                        strokeWidth={2}
                        className="cursor-pointer hover:r-6 transition-all"
                    />
                ))}

                {/* X-axis labels */}
                {points.map((p, i) => {
                    // Show every other label if too many points
                    if (data.length > 10 && i % 2 !== 0) return null;
                    return (
                        <text
                            key={i}
                            x={p.x}
                            y={height - padding.bottom + 20}
                            textAnchor="middle"
                            className="text-[10px] font-mono font-semibold fill-graphite-500"
                        >
                            {formatDate(p.date)}
                        </text>
                    );
                })}

                {/* Gradient definition */}
                <defs>
                    <linearGradient id="gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#0ea5e9" stopOpacity={0.4} />
                        <stop offset="100%" stopColor="#0ea5e9" stopOpacity={0} />
                    </linearGradient>
                </defs>
            </svg>
        </div>
    );
};
